@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow:ital,wght@1,800&family=Ubuntu+Mono:wght@400;700&display=swap);
*{box-sizing:border-box}html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}html{font-size:62.5%}h1{font-size:10rem;color:#ff0}

html{font-size:62.5%;font-family:"Ubuntu Mono", monospace}html body{font-size:3rem}.app{max-width:1600px;margin:0 auto;padding:50px 20px;min-height:100vh;display:flex;flex-direction:column;justify-content:flex-start;align-items:center}.app .tracker{width:50%}.app .tracker header{text-transform:uppercase;text-align:center;font-weight:600;font-size:5rem;padding:0 0 30px 0;font-family:"Barlow", sans-serif}@media screen and (max-width: 1276px){.app .tracker{width:70%}}@media screen and (max-width: 946px){.app .tracker{width:90%}}@media screen and (max-width: 628px){html body{font-size:2rem}.app .tracker header{font-size:4rem}}

.expenses__item{display:flex;justify-content:space-between;border:3px solid black;padding:15px;border-radius:18px;margin-bottom:20px;background-color:lightblue;cursor:pointer}

.newExpense{display:flex;flex-direction:column;margin-bottom:20px;border:3px solid black;padding:15px;border-radius:18px}.newExpense__text{display:flex;flex-direction:column;align-items:flex-start}.newExpense__amount{display:flex;flex-direction:column;align-items:flex-start}.newExpense input,.newExpense label{margin-bottom:10px}.newExpense input{height:50px;width:400px;font-size:2.8rem;border-radius:10px;padding:0 10px;border:1px solid lightgrey}.newExpense button{align-self:flex-end;padding:10px 24px;border:3px solid black;font-size:2.7rem;font-weight:500;border-radius:12px;cursor:pointer}.newExpense.invalid{border:3px solid red}@media screen and (max-width: 628px){.newExpense button{font-size:1.7rem}}@media screen and (max-width: 522px){.newExpense input{width:100%;font-size:2rem}}

.expensesTotal{border:3px solid black;padding:15px;border-radius:18px;display:flex;justify-content:space-between;margin-bottom:20px}

